import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, LoadingController, AlertController, ModalController, ToastController } from 'ionic-angular';

import { SoapService } from '../soap.service';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass } from '../../config';

/**
 * Generated class for the SurveyListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-survey-list',
  providers: [SoapService],
  templateUrl: 'survey-list.html',
})
export class SurveyListPage {
  surveyList: Array<any> = []
  userdataTPK: any;
  isLoading: Boolean = true;  
  fakeUsers: Array<any> = new Array(5);  
  batasAtas = 1;
  batasBawah = 10;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public popoverCtrl: PopoverController,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController
    ) {
      this.storage.get('userdataTPK').then(val => {
        this.userdataTPK = val;     
        this.getSurveyList('first', '');
      });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SurveyListPage');
  }

  getSurveyList(type, functionName) {        
    if (type == 'first' && functionName == '') {
      this.isLoading = true;  
    }    
    this.soapService
      .post(api_base_url, 'eoffice_survey_list', {
        fStream: JSON.stringify(
          {
            usernameEDI : api_user, 
            passwordEDI : api_pass,  
            nipp: this.userdataTPK['data']['NIPP'],
            atas: this.batasAtas,
            bawah: this.batasBawah
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));  
        console.log(responData)      ;
        if (responData['rcmsg'] == "SUCCESS") {
          if (type == 'refresh' && functionName != '') {
            this.surveyList = [];
          }
          if (responData['data'].length > 0 ) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.surveyList.push(responData['data'][i]);
            }
          }          
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data survey, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }        
        
      })
      .catch(error => {        
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan data survey, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      
        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }

        this.isLoading = false;
      });
  }

  doInfinite(infiniteScroll) {
    if (this.surveyList.length >= 10) {
      this.batasAtas = this.batasBawah + 1;
      this.batasBawah = this.batasBawah + 10;
      this.getSurveyList('infinite', infiniteScroll);
    } else {
      infiniteScroll.complete();
    }
  }

  doRefresh(refresher) {
    this.batasAtas = 1;
    this.batasBawah = 10;
    this.getSurveyList('refresh', refresher);
  }

  presentPopover(myEvent) {
    let popover = this.popoverCtrl.create("MenuSurveyPage", {
      
    }, { cssClass: "my-popover" });
    popover.present({
      ev: myEvent
    });
  }

  goToDetail(data) {    
    this.navCtrl.push("SurveyDetailPage", {
      "data": data    
    });
  }

}



